<script lang="ts">
import AiChat from '../vue/AiChat'
import Browser from "webextension-polyfill";
import {Helpers} from "../classes/Helpers";

export default {
  extends: AiChat,
  data() {
    return {
      pinned: false,
      login: false,
      openTabId: null,
      popup_try: 0,
      googlePopUp: (event, w = 515, h = 580) => {
        if (event != null) (event.target as any).setAttribute('disabled', 'disabled');
        return Helpers.apiCall('/auth/google/get_url', {}, 'POST').then(result => {
          var url = (result as any).google_auth_uri;
          //if (url == null) return this.googlePopUp(event, w, h);
          var left = (screen.width / 2) - (w / 2);
          var top = (screen.height / 2) - (h / 2);
          var targetWin = window.open(url, 'Google Auth', 'toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=' + w + ', height=' + h + ', top=' + top + ', left=' + left);
          if (event != null) (event.target as any).removeAttribute('disabled');
          this.googleLoginCheck();
          return targetWin;
        });
      },
      googleLoginCheck: () => {
        let app = (this as any);
        if (this.openTabId != null) {
          setTimeout(() => {
            Browser.tabs.get(app.openTabId).then((tab) => {
              if (tab.url.indexOf('/subscription') !== -1) {
                Browser.tabs.remove(app.openTabId);
                Helpers.uuid(1).then(() => {
                  app.login = true;
                  Browser.tabs.query({}).then((tabs) => {
                    tabs.forEach(tab => {
                      if (tab.url == app.constants.login) Browser.tabs.reload(tab.id);
                    });
                  });
                });
              } else if (tab.url.indexOf(app.constants.login) !== -1) {
                Browser.tabs.remove(app.openTabId);
                app.googlePopUp();
              } else {
                app.googleLoginCheck();
              }
            }).catch((error) => {
              if (error.toString().indexOf('No tab') !== -1) {
                app.openTabId = null;
                app.googleLoginCheck();
              }
            });
          }, 500);
        } else {
          Browser.tabs.query({}).then((tabs) => {
            tabs.forEach(tab => {
              if (tab.url.indexOf('866219478234') !== -1) {
                app.openTabId = tab.id;
              }
            });
            if (app.openTabId == null) app.popup_try = app.popup_try + 1;
            if (app.openTabId == null && app.popup_try > 3) {
              app.popup_try = 0;
              return;
            }
            setTimeout(() => {
              this.googleLoginCheck();
            }, 500);
          });
        }
      },
      login_type: 'register',
      forms: {
        email: null,
        password: null,
        password1: null,
        agree: false,
      },
    };
  },
  created() {
    this.$watch(() => this.login, () => {
      if (this.login == true && this.pinned == true) {
        Browser.tabs.query({}).then((tabs) => {
          tabs.forEach(tab => {
            if (tab.url == this.constants.login) Browser.tabs.reload(tab.id);
          });
        });
        this.sidebarToggle(true);
      }
    });
    this.$watch(() => this.pinned, () => {
      if (this.login == true && this.pinned == true) {
        Browser.tabs.query({}).then((tabs) => {
          tabs.forEach(tab => {
            if (tab.url == this.constants.login) Browser.tabs.reload(tab.id);
          });
        });
        this.sidebarToggle(true);
      }
    });
  },
  mounted() {
    const url = window.location.href;

    if (url.toString().endsWith('side-bar-option=true')) {
      this.sidebarToggle(true);
    } else {
      this.sidebarToggle(false);
    }

    let app = this;
    let setupInterVal = setInterval(() => {
      Browser.action.getUserSettings().then((settings) => {
        if ((settings.isOnToolbar ?? false) == true) {
          app.pinned = true;
          if (setupInterVal) clearInterval(setupInterVal);
        }
      });
      if (typeof app.user != 'undefined') if (typeof app.user.email != 'undefined') app.login = true;
    }, 1000);
  },
}
</script>
